import { gql } from '@getvim-os/gql-client';

export const GET_USER_INTERACTIONS_QUERY = () => gql`
  query UserInteractions($input: UserInteractionsInput!) {
    userInteractions(input: $input) {
      appId
      interactionType
      lastInteractionDate
    }
  }
`;

export const SAVE_USER_INTERACTIONS_MUTATION = gql`
  mutation CreateBulkUserInteractions($input: CreateBulkUserInteractionsInput!) {
    createBulkUserInteractions(input: $input)
  }
`;
