import { injectable } from 'inversify';
import { IEhrAdapter } from '@getvim-adapter/types';
import { NotFoundError } from '@getvim-os/errors';
import { AdapterV2UrlResolver } from './adapterV2UrlResolver';

const RETRY = 4;
const BASE_RETRY_DELAY = 400;

@injectable()
export class AdapterV2InjectionService {
  public async injectAdapter(ehrName: string, enableCaching: boolean = true): Promise<IEhrAdapter> {
    return await new Promise<IEhrAdapter>((resolve, reject) => {
      this.setupWindowPorts();

      const urls: string[] = [];
      let iteration = 0;
      let scriptElement: HTMLScriptElement;

      const onAdapterLoaded = async () => {
        const adapter = (window as any).adapter;
        if (!adapter) {
          if (iteration > RETRY) {
            reject(
              new NotFoundError('Failed to load adapter', {
                urls,
              }),
            );
          }
          scriptElement.remove();
          setTimeout(loadAdapterScript, this.calculateRetryDelay(iteration));
        } else {
          resolve(adapter);
        }
      };

      const failToLoad = async () => {
        if (iteration > RETRY) {
          await this.handleLoadFailure(ehrName, enableCaching, urls).catch((error) =>
            reject(error),
          );
          return;
        }
        scriptElement.remove();
        setTimeout(loadAdapterScript, this.calculateRetryDelay(iteration));
      };

      const loadAdapterScript = () => {
        iteration++;
        scriptElement = this.createScriptElement(
          ehrName,
          enableCaching,
          urls,
          onAdapterLoaded,
          failToLoad,
        );
        document.head.append(scriptElement);
      };

      loadAdapterScript();
    });
  }

  public isAdapterV2Supported(adapterId: string): boolean {
    return !['ecw-desktop', 'nextgen-desktop', 'simulator', 'vimulator'].includes(adapterId);
  }

  private setupWindowPorts(): void {
    (window as any).$barebonePort =
      (window as any).$vimConnectParentPort === undefined
        ? (window as any).$barebonePort
        : (window as any).$vimConnectParentPort;
  }

  private createScriptElement(
    ehrName: string,
    enableCaching: boolean,
    urls: string[],
    onLoad: () => void,
    onError: () => void,
  ): HTMLScriptElement {
    const adapterV2Url = AdapterV2UrlResolver.resolveUrl(ehrName, enableCaching);
    urls.push(adapterV2Url);

    const scriptElement = document.createElement('script');
    scriptElement.addEventListener('load', onLoad);
    scriptElement.addEventListener('error', onError);
    scriptElement.addEventListener('readystatechange', onLoad);
    scriptElement.src = adapterV2Url;
    scriptElement.type = 'module';

    return scriptElement;
  }

  private async handleLoadFailure(
    ehrName: string,
    enableCaching: boolean,
    urls: string[],
  ): Promise<never> {
    let fetchErrorResult;
    try {
      const adapterV2Url = AdapterV2UrlResolver.resolveUrl(ehrName, enableCaching);
      urls.push(adapterV2Url);
      fetchErrorResult = await fetch(adapterV2Url);
      fetchErrorResult = await fetchErrorResult.text();
    } catch (error) {
      fetchErrorResult = error;
    }
    throw new NotFoundError(fetchErrorResult, {
      urls,
    });
  }

  private calculateRetryDelay(iteration: number): number {
    return BASE_RETRY_DELAY * Math.pow(2, iteration);
  }
}
