import { injectable, inject } from 'inversify';
import { createPromisedChannel } from 'msg-channel-shake/core';
import { ActionNames } from '@getvim/vim-os-api';
import { OsMessageChannelManager } from '../deprecated/runtime-wrapper/osMessageChannelManager';
import { Events } from '@deprecated/types';
import { VimSpan } from '@getvim-os/telemetry';

@injectable()
export class ActionsManager {
  private DEFAULT_ACTION_TIMEOUT_MS: number = 10_000;

  constructor(
    @inject(OsMessageChannelManager) private osMessageChannelManager: OsMessageChannelManager,
  ) {}

  public async executeActionPromised<TInput, TResponse>(
    action: ActionNames,
    input: TInput,
    span?: VimSpan,
  ): Promise<TResponse> {
    const [portToForward, promise] = createPromisedChannel<TResponse>(
      this.DEFAULT_ACTION_TIMEOUT_MS,
    );
    this.executeAction(action, input, portToForward);

    try {
      return await (span?.startAutoChildSpan('executeAction', {}, () => promise) ?? promise);
    } catch (error: any) {
      const message = `Error while getting result from action ${action}!`;
      if (error?.message) {
        throw new Error(`${message} ${error?.message}`, {
          cause: error,
        });
      }
      if (typeof error === 'string') {
        // eslint-disable-next-line unicorn/prefer-type-error
        throw new Error(`${message} ${error}`, {
          cause: error,
        });
      }
      throw new Error(message, { cause: error });
    }
  }

  public executeAction(action: ActionNames, input: unknown, responseMessagePort: MessagePort) {
    try {
      this.osMessageChannelManager.sendMessageToRuntime(
        {
          type: Events.OsToRuntimeEvents.EXECUTE_ACTION,
          payload: {
            action,
            input,
          },
        },
        [responseMessagePort],
      );
    } catch (error) {
      responseMessagePort.postMessage({
        error: true,
        response: (error as Error)?.message,
      });

      return;
    }
  }
}
