import { gql } from '@getvim-os/gql-client';

export const GET_MARKETPLACE_APPLICATIONS_QUERY = (operationName: string) => gql`
  query ${operationName} {
    getAllMarketplaceApplications {
      applications {
        account {
          name
          address
          phoneNumber   
        }
        id
        name
        submission {
          appStoreListing
          permissionUsage
          securityAndCompliance
          marketplaceSettings
        }
        isCoreApp
        isInstalled
        dataConnections {
          name
          iconUrl
          id
          enum
          connectionType
        }
        manifest {
          metadata {
            name
            description
            url
            services {
              id
              metadata {
                id
                description
                url
              }
              capabilities {
                enrichEHR {
                  allowedPII
                  resources {
                    patient
                    encounter
                    orders
                    referral
                  }
                }
                enrichUser
                custom
                allowedPII
                actions
              }
            }
            contactEmailSupport
            termsOfUseUrl
            launchEndpoint
            tokenEndpoint
            allowedUrls
          }
          session {
            permissions
          }
          UI {
            icon
            iconUrl
            tooltip
            size
            capabilities {
              showPatientHeader
              counterNotificationLabel
              popupNotification
              autoPopup
              overridePopupLimit
              appAlwaysAvailable
            }
            launchButton {
              ... on CustomLaunchButton {
                customHTML
              }
              ... on ConfigurableLaunchButton {
                irrelevantVisibility
                alt
                primaryColor
                borderColor
                fontColor
                mode {
                  small {
                    text
                    image
                  }
                  medium {
                    text
                    image
                  }
                  large {
                    text
                    image
                  }
                }
              }
            }
          }
          EHR {
            resourcePermissions
            allowedPII
            actions {
              FIND_PATIENT
              GET_PATIENT
              GET_ENCOUNTER_DATA
              GET_CURRENT_EHR_USER_INFORMATION
              GET_PATIENT_ENCOUNTERS
              GET_PROVIDER_DAILY_SUMMARY
              GET_PATIENT_SCHEDULED_APPOINTMENTS
              PRINT_ENCOUNTER
              PRINT_ENCOUNTERS
              SEARCH_PROVIDERS
              GET_ENCOUNTER_VITALS
              GET_ENCOUNTER_LAB_RESULTS
              NAVIGATE_TO_ENCOUNTER
              GET_PROVIDER_APPOINTMENTS
              GET_GAPS_FROM_SHARED_REQUEST
            }
            launchButtonRealEstate {
              referral {
                REFERRAL_SELECT_PROVIDER
              }
              orders {
                DI_SELECT_PROVIDER
                LABS_SELECT_PROVIDER
              }
            }
          }
          auth {
            vimTokens
          }
          consoleUI {
            url
          }
          vimCapabilities {
            trackAnalytics
          }
        }
      }
    }
  }
`;

export const SUBMIT_REQUEST_FOR_APP = gql`
  mutation RequestToAddApplication($input: RequestToAddApplicationInput!) {
    requestToAddApplication(input: $input) {
      success
    }
  }
`;

export const USER_CAN_ACCESS_MARKETPLACE_QUERY = gql`
  query CanAccessMarketPlace {
    canAccessMarketplace {
      canAccessMarketplace
    }
  }
`;
