/* eslint-disable @typescript-eslint/no-unused-vars */

export enum OrdersEnhancements {}

// eslint-disable-next-line @typescript-eslint/ban-types
export type OrdersEnhancementsAPI = {};

/**
 * THIS Types below are used to verify that the OrdersEnhancementsAPI
 * uses all the properties from the enum & the other way around
 *
 * The reason is using mapped types "[OrdersEnhancements.GET_PROBLEM_LIST]()" instead of "getProblemList()"
 * creates bad autocomplete typings
 */
type EnumValues = `${OrdersEnhancements}`;

type VerifyEnumMatchesAPI = keyof OrdersEnhancementsAPI extends EnumValues ? true : never;
type VerifyAPIMatchesEnum = OrdersEnhancements extends keyof OrdersEnhancementsAPI ? true : never;

// These will cause type errors if there's a mismatch
const enumMatchesAPI: VerifyEnumMatchesAPI = true;

// @ts-expect-error - Will be valid once enum is populated (not working for empty enum)
const apiMatchesEnum: VerifyAPIMatchesEnum = true;
