import { Logger, LoggerFactory } from '@getvim-os/logger';
import { inject, injectable } from 'inversify';
import { PutChartRequestInput, PutChartRequestResponse } from './types';
import { PUT_CHART_REQUEST_QUERY } from './mutations';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';

@injectable()
export class PutChartRequestApi {
  private logger: Logger;

  constructor(
    @inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
  ) {
    this.logger = loggerFactory.create(PutChartRequestApi.name);
  }

  public chartRequest = async (
    putChartRequestInput: PutChartRequestInput,
  ): Promise<PutChartRequestResponse> => {
    const { data } = await this.gqlClient.mutate<{ putChartRequest: PutChartRequestResponse }>({
      mutation: PUT_CHART_REQUEST_QUERY,
      variables: { input: putChartRequestInput },
    });

    return data!.putChartRequest;
  };
}
