import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import { AddEventListenerResult } from '../../types/addEventListenerResult';
import { CaptureAudioInput } from '../../types/captureAudio';

export async function captureAudio(
  this: WebConnectors,
  { selector, frameSelector, handler }: CaptureAudioInput,
): Promise<AddEventListenerResult> {
  const listenerId = await this.messaging.sendMessageWithHandler({
    type: WebConnectorMessageTypes.captureAudio,
    payload: { selector, frameSelector },
    handler,
  });
  return {
    remove: () => this.removeEventListener({ listenerId }),
    listenerId,
  };
}
