import { inject, injectable } from 'inversify';
import { ApplicationVersionAPI } from './application-version';
import { OrganizationsApi } from './organizations';
import { SendSupportEmailApi } from './support-email';
import { UserAPI } from './user';
import { VaultApi } from './vault';
import { WritebackAuditLoggerApi } from './writeback-audit-logger';
import { VimAppAuthApi } from './application-auth';
import { MarketPlaceApplicationsAPI } from './marketplace-applications';
import { PutChartRequestApi } from './put-chart-request';
import { SignupApi } from './signup';
import { UserInteractionsAPI } from './user-interactions';

@injectable()
export class InternalApi {
  @inject(OrganizationsApi) public organizations!: OrganizationsApi;
  @inject(WritebackAuditLoggerApi) public writebackAuditLogger!: WritebackAuditLoggerApi;
  @inject(VaultApi) public vaultApi!: VaultApi;
  @inject(ApplicationVersionAPI) public applicationVersionAPI!: ApplicationVersionAPI;
  @inject(UserAPI) public userAPI!: UserAPI;
  @inject(SendSupportEmailApi) public sendSupportEmailAPI!: SendSupportEmailApi;
  @inject(VimAppAuthApi) public vimAppAuthApi!: VimAppAuthApi;
  @inject(MarketPlaceApplicationsAPI)
  public marketPlaceApplicationsAPI!: MarketPlaceApplicationsAPI;
  @inject(PutChartRequestApi) public putChartRequestApi!: PutChartRequestApi;
  @inject(SignupApi) public signupApi!: SignupApi;
  @inject(UserInteractionsAPI) public userInteractionsAPI!: UserInteractionsAPI;
}
