import { inject, injectable } from 'inversify';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import { GetMarketplaceApplicationsResponse } from './marketplaceApplicationTypes';
import {
  GET_MARKETPLACE_APPLICATIONS_QUERY,
  SUBMIT_REQUEST_FOR_APP,
  USER_CAN_ACCESS_MARKETPLACE_QUERY,
} from './marketplaceApplicationsQueries';
@injectable()
export class MarketPlaceApplicationsAPI {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async getApplications(operationName: string): Promise<GetMarketplaceApplicationsResponse> {
    const result = await this.gqlClient.queryAuthenticated<{
      getAllMarketplaceApplications: GetMarketplaceApplicationsResponse;
    }>({
      query: GET_MARKETPLACE_APPLICATIONS_QUERY(operationName),
    });
    return result.data.getAllMarketplaceApplications;
  }
  public async submitApplicationAccessRequest(
    applicationId: string,
    dataSources: Array<string> | undefined,
  ): Promise<any> {
    const result = await this.gqlClient.mutateAuthenticated<{
      submitRequestForApp: any;
      appId: string;
    }>({
      mutation: SUBMIT_REQUEST_FOR_APP,
      variables: { input: { appId: applicationId, dataSources } },
    });
    return result.data;
  }

  async canAccessMarketPlace(): Promise<boolean> {
    const res = await this.gqlClient.queryAuthenticated<{
      canAccessMarketplace: { canAccessMarketplace: boolean };
    }>({
      query: USER_CAN_ACCESS_MARKETPLACE_QUERY,
    });
    return res.data.canAccessMarketplace.canAccessMarketplace;
  }
}
