import { OsMessageChannelManager } from '@/deprecated/runtime-wrapper/osMessageChannelManager';
import { getAdapterId } from '@/infra/consts';
import {
  EhrManifest,
  EntitiesRequest,
  EntitiesRequestSchema,
  OsToAdapterEvents,
} from '@getvim-adapter/api';
import { InternalError } from '@getvim-os/errors';
import { AdapterV2InjectionService, AdapterID } from '@getvim-os/adapter';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { ApplicationManifest } from '@getvim-os/types';
import { inject, injectable } from 'inversify';
import { createPromisedChannel } from 'msg-channel-shake/core';
import { RuntimeWrapper } from '../deprecated/runtime-wrapper';
import { OsFeatureFlagsClient } from '../infra/featureFlags';
import { ActionsManager } from './actionsManager';
import { ehrManifestSupportMap } from './manifest-support/mapping';
import { ResourceEnhancementsManager } from './resourceEnhancementManager';
import { ResourceUpdateManager } from './resourceUpdateManager';
import {
  ADAPTER_SOAP_SUPPORT,
  DEFAULT_SOAP_SUPPORT,
  SupportsBasicWriteBacks,
} from './updateResourceMapping';
import { IEhrAdapter } from '@getvim-adapter/types';
import { VimSpan, VimTracerProvider } from '@getvim-os/telemetry';
import { OrganizationDetails } from '@/internal-api/organizations/types';

@injectable()
export class AdapterManager {
  public adapterId: AdapterID;
  private logger: Logger;
  private manifestPromise?: Promise<EhrManifest | undefined>;
  private adapter: IEhrAdapter | undefined;
  private adapterManagerSpan: VimSpan;
  private adapterPromise: Promise<IEhrAdapter> | undefined;
  private adapterResolve: ((adapter: IEhrAdapter) => void) | undefined;

  constructor(
    @inject(RuntimeWrapper) private runtimeWrapper: RuntimeWrapper,
    @inject(OsFeatureFlagsClient) private ffClient: OsFeatureFlagsClient,
    @inject(ActionsManager) public actionManager: ActionsManager,
    @inject(ResourceEnhancementsManager)
    public resourceEnhancementsManager: ResourceEnhancementsManager,
    @inject(ResourceUpdateManager) public resourceUpdateManager: ResourceUpdateManager,
    @inject(LoggerFactory) loggerFactory: LoggerFactory,
    @inject(OsMessageChannelManager) private osMessageChannelManager: OsMessageChannelManager,
    @inject(AdapterV2InjectionService) private adapterV2InjectionService: AdapterV2InjectionService,
    @inject(VimTracerProvider) private traceProvider: VimTracerProvider,
  ) {
    this.logger = loggerFactory.create('adapter-manager');
    const tracer = this.traceProvider.getTracer('adapter-manager');
    this.adapterManagerSpan = tracer.startRootSpan('adapter-manager');
    try {
      this.adapterId = getAdapterId();
    } catch (error) {
      throw new InternalError('Failed to extract adapterID from query params', error);
    }
  }

  public getSoapSupport(): SupportsBasicWriteBacks {
    return ADAPTER_SOAP_SUPPORT[this.adapterId] ?? DEFAULT_SOAP_SUPPORT;
  }

  public async hasDynamicEncounterUpdate(): Promise<boolean> {
    const manifest = await this.getManifest();
    const hasDynamicEncounterUpdate = manifest?.entities?.editableEncounter !== undefined;
    return hasDynamicEncounterUpdate;
  }

  private async shouldInjectAdapterV2FromVimOS(organizationId?: string): Promise<boolean> {
    return await this.ffClient.getFlag({
      flagName: 'inject_adapter_v2_from_vim_os',
      defaultValue: false,
      flagContext: {
        adapterName: this.adapterId,
        organizationId_string: organizationId,
      },
    });
  }

  public async hasDynamicReferralUpdate(): Promise<boolean> {
    const manifest = await this.getManifest();
    const editableReferral = manifest?.entities?.editableReferral !== undefined;
    return editableReferral;
  }

  private getManifest(): Promise<EhrManifest | undefined> {
    if (!this.manifestPromise) {
      this.manifestPromise = new Promise<EhrManifest | undefined>((resolve) => {
        (async () => {
          const [portToForward, promise] = createPromisedChannel<EhrManifest>();

          await this.osMessageChannelManager.sendMessageToRuntime(
            {
              type: OsToAdapterEvents.Enum.GET_ADAPTER_MANIFEST,
            },
            [portToForward],
          );
          const manifest = await promise;
          resolve(manifest);
        })().catch((error) => {
          this.logger.error('Failed to load manifest from adapter', error);
          resolve(undefined);
        });
      });
    }
    return this.manifestPromise;
  }

  private async shouldInjectAdapterV2(organizationId?: string) {
    const isAdapterV2Supported = this.adapterV2InjectionService.isAdapterV2Supported(
      this.adapterId,
    );
    const shouldInjectAdapterV2FromVimOS = await this.shouldInjectAdapterV2FromVimOS(
      organizationId,
    );
    const shouldInjectAdapterV2 = isAdapterV2Supported && shouldInjectAdapterV2FromVimOS;
    return shouldInjectAdapterV2;
  }

  public async loadAdapterV2(organizationDetails?: OrganizationDetails): Promise<void> {
    await this.adapterManagerSpan.startAutoChildSpan('init-v2-adapter', {}, async () => {
      this.logger.info('Using new adapter from VimOS - For login purposes only');
      const shouldInjectAdapterV2 = await this.shouldInjectAdapterV2(
        organizationDetails?.organizationId?.toString(),
      );
      if (!shouldInjectAdapterV2) {
        this.logger.warning('New adapter is not supported', {
          adapterId: this.adapterId,
          organizationId: organizationDetails?.organizationId,
        });
        throw new InternalError('New adapter is not supported', {
          adapterId: this.adapterId,
          organizationId: organizationDetails?.organizationId,
        });
      }
      await this.adapterV2InjectionService.injectAdapter(this.adapterId);
      this.adapter = (window as any).adapter as IEhrAdapter;
      const { organizationId, organizationName } = organizationDetails ?? {};
      if (organizationId && organizationName) {
        this.adapter.setOrganization({
          organizationId: organizationId.toString(),
          organizationName: organizationName,
        });
      }
      if (this.adapterResolve) {
        this.adapterResolve(this.adapter);
      }
    });
  }

  public async getAdapterV2(timeout = 3000): Promise<IEhrAdapter> {
    if (this.adapter) {
      return this.adapter;
    }

    if (!this.adapterPromise) {
      this.adapterPromise = new Promise<IEhrAdapter>((resolve) => {
        this.adapterResolve = resolve;
      });
    }

    if (timeout) {
      return await Promise.race([
        this.adapterPromise,
        new Promise<IEhrAdapter>((_, reject) =>
          setTimeout(() => reject(new Error('Adapter loading timed out')), timeout),
        ),
      ]);
    }

    return await this.adapterPromise;
  }

  public async stopAdapterV2() {
    if (this.adapter) {
      this.logger.info('Stopping new adapter from VimOS', {
        adapter: this.adapter,
      });
      this.adapter.stop();
    }
  }

  public async loadRuntime(organizationDetails?: OrganizationDetails): Promise<void> {
    await this.adapterManagerSpan.startAutoChildSpan('load-runtime', {}, async () => {
      this.logger.info('injecting adapter with old RuntimeWrapper');
      await this.runtimeWrapper.loadRuntime(
        organizationDetails?.organizationId?.toString(),
        organizationDetails?.organizationName,
        organizationDetails?.organizationTag,
      );
    });
    this.adapterManagerSpan.end();
  }

  public async setActiveResources(requestedEntities: EntitiesRequest) {
    const parseResult = EntitiesRequestSchema.safeParse(requestedEntities);

    if (parseResult.success) {
      await this.osMessageChannelManager.sendMessageToRuntime({
        type: OsToAdapterEvents.Enum.SET_ACTIVE_ENTITIES,
        payload: parseResult.data,
      });
    } else {
      this.logger.error('Failed to parse set active entities request', parseResult.error.format());
    }
  }

  public getManifestSupport(): Pick<ApplicationManifest, 'EHR'> {
    return ehrManifestSupportMap[this.adapterId];
  }
}
