import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import {
  BackboneDoesFunctionExistsResult,
  GetBackboneViewFunctionInput,
} from '../../types/backboneInvokeFunction';

export async function backboneDoesFunctionExists(
  this: WebConnectors,
  payload: GetBackboneViewFunctionInput,
): Promise<BackboneDoesFunctionExistsResult> {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.backboneDoesFunctionExists,
    payload,
  });
}
