import { createSlice } from '@reduxjs/toolkit';
import { PayloadAction } from '..';
import { LoginState, EhrCredentials } from '@/login/types';
import { OrganizationDataOnDevice, OrganizationDetails } from '@/internal-api/organizations/types';

type Login = {
  ehrUserLoggedIn: boolean;
  ehrUserCredentials: EhrCredentials;
  loginState: LoginState;
  shouldHandleLogin: boolean | undefined;
  shouldHandleSignup: boolean | undefined;
  organization: {
    dataOnDevice: (OrganizationDataOnDevice & { organizationId?: number }) | undefined;
    details: OrganizationDetails | undefined;
  };
};

export const initialState: Login = {
  ehrUserLoggedIn: false,
  ehrUserCredentials: {
    ehrUsername: '',
    ehrUserPassword: '',
  },
  loginState: LoginState.INITIALIZING,
  shouldHandleLogin: undefined,
  shouldHandleSignup: undefined,
  organization: {
    dataOnDevice: undefined,
    details: undefined,
  },
};

export const slice = createSlice({
  name: 'login',
  initialState,
  reducers: {
    setEhrUserLoggedIn: (state, action: PayloadAction<Login['ehrUserLoggedIn']>) => {
      state.ehrUserLoggedIn = action.payload;
    },
    setEhrUserCredentials: (state, action: PayloadAction<Login['ehrUserCredentials']>) => {
      state.ehrUserCredentials = action.payload;
    },
    setLoginState: (state, action: PayloadAction<Login['loginState']>) => {
      state.loginState = action.payload;
    },
    setShouldHandleLogin: (state, action: PayloadAction<Login['shouldHandleLogin']>) => {
      state.shouldHandleLogin = action.payload;
    },
    setShouldHandleSignup: (state, action: PayloadAction<Login['shouldHandleSignup']>) => {
      state.shouldHandleSignup = action.payload;
    },
    setOrganizationDataOnDevice: (
      state,
      action: PayloadAction<Login['organization']['dataOnDevice']>,
    ) => {
      state.organization.dataOnDevice = action.payload;
    },
    setOrganizationDetails: (state, action: PayloadAction<Login['organization']['details']>) => {
      state.organization.details = action.payload;
    },
  },
});

export const actions = slice.actions;

export default slice.reducer;
