import { isEmpty } from 'lodash-es';
const DEFAULT_LOCAL_ADAPTER_URL = 'http://localhost:7023';

export class AdapterV2UrlResolver {
  private static isKnownLocalEnv(origin: string): boolean {
    const developmentUrls: string[] = [
      'localhost',
      '10.211.55.2',
      '.sish.dev.getvim.com',
      '.localhost.getvim.com',
      'your_mac_ip:',
    ];
    return developmentUrls.some((urlPart: string) => origin.includes(urlPart));
  }

  private static isKnownExternalEnv(hostname: string): boolean {
    return hostname.includes('devim.io') || hostname.includes('getvim.com');
  }

  private static shouldOverrideAdapterUrl(): boolean {
    const searchParams = new URLSearchParams(window.location.search);
    const shouldOverrideUrl = searchParams.get('override-v2-url') === 'true';
    const isProd = (window as any).$vim_environment?.APP_ENV === 'prod';
    const isKnownEnv =
      this.isKnownLocalEnv(window.location.origin) ||
      this.isKnownExternalEnv(window.location.hostname);

    return shouldOverrideUrl && isKnownEnv && !isProd;
  }

  private static getLocalAdapterUrl(): string {
    const searchParams = new URLSearchParams(window.location.search);
    const overrideUrl = searchParams.get('v2-adapter-local-url')?.trim();
    const baseUrl = isEmpty(overrideUrl) ? DEFAULT_LOCAL_ADAPTER_URL : overrideUrl;
    return `${baseUrl}/src/main.ts`;
  }

  private static getQueryString(enableCaching: boolean): string {
    return enableCaching ? '' : `?rand=${Date.now()}`;
  }

  public static resolveUrl(ehrName: string, enableCaching: boolean = true): string {
    const url = new URL(window.location.href);
    const searchParams = new URLSearchParams(window.location.search);

    if (this.isKnownLocalEnv(window.location.origin) || this.shouldOverrideAdapterUrl()) {
      if (searchParams.has('pid')) {
        // desktop
        const queryString = this.getQueryString(enableCaching);
        return `http://${url.hostname}:7023/src/main.ts${queryString}`;
      }

      return this.getLocalAdapterUrl();
    } else if (this.isKnownExternalEnv(url.hostname)) {
      const splittedHostname = url.hostname.split('.');
      splittedHostname.splice(0, 1, 'connect');
      const joinedArray = splittedHostname.join('.');

      const queryString = this.getQueryString(enableCaching);
      const encodedAdapterVersion = searchParams.get('adapterVersion');

      if (encodedAdapterVersion) {
        try {
          const adapterVersion = window.atob(encodedAdapterVersion);
          const resolvedUrl = `https://${joinedArray}/${ehrName}/${adapterVersion}/adapter.js${queryString}`;
          // TODO: use logger
          console.debug('Resolved adapter URL with version', {
            url: resolvedUrl,
            ehrName,
            enableCaching,
            adapterVersion,
          });
          return resolvedUrl;
        } catch {
          console.error('Received invalid adapter version parameter', {
            encodedAdapterVersion,
            url: window.location.href,
            ehrName,
          });
          const resolvedUrl = `https://${joinedArray}/${ehrName}/adapter.js${queryString}`;
          console.debug('Resolved adapter URL without version', {
            url: resolvedUrl,
            ehrName,
            enableCaching,
          });
          return resolvedUrl;
        }
      } else {
        const resolvedUrl = `https://${joinedArray}/${ehrName}/adapter.js${queryString}`;
        console.debug('Resolved adapter URL', { url: resolvedUrl, ehrName, enableCaching });
        return resolvedUrl;
      }
    }

    throw new Error(`Could not resolve URL to vim environment: ${url.hostname}`);
  }
}
