import { WebConnectors } from '../..';
import { WebConnectorMessageTypes } from '../../enum';
import {
  BackboneInvokeChainFunctions,
  BackboneInvokeFunction,
  BackboneInvokeFunctionResult,
} from '../../types/backboneInvokeFunction';

export async function backboneInvoke(
  this: WebConnectors,
  payload: BackboneInvokeFunction,
): Promise<BackboneInvokeFunctionResult> {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.backboneInvoke,
    payload,
  });
}

export async function backboneInvokeChain(
  this: WebConnectors,
  payload: BackboneInvokeChainFunctions,
): Promise<BackboneInvokeFunctionResult> {
  return await this.messaging.sendMessage({
    type: WebConnectorMessageTypes.backboneInvokeChain,
    payload,
  });
}
