import { inject, injectable } from 'inversify';
import { VimOsBffGraphQLClient } from '../vimOsBffGqlClient';
import {
  GET_USER_INTERACTIONS_QUERY,
  SAVE_USER_INTERACTIONS_MUTATION,
} from './userInteractionsQueries';
import {
  UserInteraction,
  UserInteractionInput,
  UserInteractionsInput,
} from './userInteractionsQueries.types';

@injectable()
export class UserInteractionsAPI {
  constructor(@inject(VimOsBffGraphQLClient) private gqlClient: VimOsBffGraphQLClient) {}

  public async getUserInteractions(input: UserInteractionsInput) {
    const result = await this.gqlClient.queryAuthenticated<{
      userInteractions: UserInteraction[];
    }>({
      query: GET_USER_INTERACTIONS_QUERY(),
      variables: {
        input,
      },
    });
    return result.data.userInteractions;
  }

  public async saveUserInteractions(userInteractions: UserInteractionInput[]) {
    await this.gqlClient.mutateAuthenticated<{
      createBulkUserInteractions: any;
    }>({
      mutation: SAVE_USER_INTERACTIONS_MUTATION,
      variables: { input: { userInteractions } },
    });
    return true;
  }
}
