import React from 'react';
import classNames from 'classnames';
import { UI } from '@deprecated/types';
import './NewMenuItemBadge.less';

interface NewMenuItemBadgeProps {
  hubPosition: UI.HubPosition;
}

export const NewMenuItemBadge: React.FC<NewMenuItemBadgeProps> = ({ hubPosition }) => {
  return (
    <div
      className={classNames('new-menu-item-badge', {
        left: hubPosition.horizontal === UI.HorizontalHubPosition.Left,
        right: hubPosition.horizontal === UI.HorizontalHubPosition.Right,
      })}
    >
      New
    </div>
  );
};

NewMenuItemBadge.displayName = 'NewMenuItemBadge';

export default NewMenuItemBadge;
