import { Attributes } from '@opentelemetry/api';
import { LogAttributes } from '@opentelemetry/api-logs';
import { inject, injectable } from 'inversify';
import { TelemetryGlobalAttributes } from './globalAttributes';
import { TelemetryScopedAttributes } from './scopedAttributes';
import { VimAttributes } from './types';

@injectable()
export class AttributesInjector {
  private allowedMetricKeys: Array<keyof VimAttributes> = [
    'appId',
    'appName',
    'bareboneType',
    'bareboneVersion',
    'ehrSystem',
    'env',
    'organizationId',
    'organizationName',
    'version',
    'is_editable',
    'is_defined',
    'entity',
    'field',
    'success',
    'name',
    'timing',
    'block',
    'url',
    'host',
    'source',
    'finish',
    'action_name',
    'reason',
    'nodeName', // adapter engine
    'configName', // adapter engine
    'outputSection', // adapter engine
    'configVersion', // adapter engine
  ];

  private importantOrgsIds: Set<string> = new Set([
    '182',
    '118',
    '191',
    '1151',
    '980',
    '26',
    '186',
    '1969',
    '1361',
    '235',
    '1037',
    '1930',
    '730',
  ]);

  private importantOrgsNames: Set<string> = new Set([
    'WellMed',
    'VitalMD',
    'Max Health',
    'Avance Care - Aledade',
    'archwell',
    'Prima-Care',
    'North Hudson Community Health Center',
    'ConcertoCare',
    'Blue Ridge Community Health',
    'Depaul Community Health Center',
    'Neuroscience Consultants',
    'Central City Community Health',
    'MyCare Enterprise',
  ]);

  constructor(
    @inject(TelemetryGlobalAttributes) private globalAttributes: TelemetryGlobalAttributes,
    @inject(TelemetryScopedAttributes) private scopedAttributes: TelemetryScopedAttributes,
  ) {}

  public injectToAttributesObj(attributes?: Attributes | LogAttributes): Attributes {
    return {
      ...(attributes ?? {}),
      ...this.globalAttributes.attributes,
      ...this.scopedAttributes.attributes,
    };
  }

  public injectToMetricsAttributesObj(attributes?: VimAttributes): Attributes {
    const mergedAttributes = {
      ...(attributes ?? {}),
      ...this.globalAttributes.attributes,
      ...this.scopedAttributes.attributes,
    };

    const result = Object.entries(mergedAttributes).reduce((acc, [key, value]) => {
      if (this.allowedMetricKeys.includes(key as keyof VimAttributes)) {
        acc[key] = this.mapOrganization(key as keyof VimAttributes, value as string);
      }
      return acc;
    }, {} as Record<string, string>);

    return result;
  }

  public mapOrganization(key: keyof VimAttributes, value: string): string {
    if (key === 'organizationId') {
      return this.importantOrgsIds.has(value) ? value : '999';
    }
    if (key === 'organizationName') {
      return this.importantOrgsNames.has(value) ? value : 'other';
    }
    return value;
  }
}
