import React, {
  forwardRef,
  PropsWithChildren,
  useCallback,
  useImperativeHandle,
  useState,
} from 'react';
import VimIcon from '../../assets/VimIcon.svg';
import helpIcon from './images/helpIcon.svg';
import './index.less';
import { Button, Text, Tooltip, usePopoverState } from '@getvim/atomic-ui';
import { links } from '@getvim/platform-consts';
import { SupportTab } from '@/atomic/molecules/support-tab';
import {
  onClosingSupportTab,
  onContactSupportSubmit,
  onOpeningSupportTab,
  onMouseHoverNeedHelp,
} from '@/constants/supportTab';
import { SupportFlowLocation } from '@/analytics-manager/events';

export interface VimHubFooterProps {
  onContactSupportSubmit: onContactSupportSubmit;
  onClosingSupportTab: onClosingSupportTab;
  onOpeningSupportTab: onOpeningSupportTab;
  onMouseHoverNeedHelp: onMouseHoverNeedHelp;
}

export interface VimHubFooterRef {
  openSupportTab: () => void;
  closeSupportTab: (closingMethod: string) => void;
}

export const VimHubFooter = forwardRef<VimHubFooterRef, PropsWithChildren<VimHubFooterProps>>(
  (
    { onContactSupportSubmit, onClosingSupportTab, onOpeningSupportTab, onMouseHoverNeedHelp },
    ref,
  ) => {
    const popover = usePopoverState({ placement: 'bottom-start' });
    const [isPopoverOpen, setIsPopoverOpen] = useState(false);

    const openSupportTab = useCallback(() => {
      popover.show();
      setIsPopoverOpen(true);
      onOpeningSupportTab(SupportFlowLocation.IN_FOOTER);
    }, [popover, onOpeningSupportTab]);

    const closeSupportTab = useCallback(
      (closingMethod) => {
        if (isPopoverOpen) {
          popover.hide();
          setIsPopoverOpen(false);
          onClosingSupportTab(closingMethod, SupportFlowLocation.IN_FOOTER);
        }
      },
      [onClosingSupportTab, popover, isPopoverOpen],
    );

    const onMouseOver = useCallback(() => {
      onMouseHoverNeedHelp(SupportFlowLocation.IN_FOOTER);
    }, [onMouseHoverNeedHelp]);

    useImperativeHandle(
      ref,
      () => ({
        openSupportTab,
        closeSupportTab,
      }),
      [closeSupportTab, openSupportTab],
    );

    return (
      <>
        <footer className={'vim-hub-footer'}>
          <div className="hub-footer-content">
            <a
              className="margin-left-10"
              href="https://getvim.com/"
              target="_blank"
              rel="noreferrer"
            >
              <img src={VimIcon} height="11" width="auto" alt="vim icon" />
            </a>
            <Text className="footer-links" inline>
              <a href={links.vimTOS} target="_blank" rel="noreferrer" className="text-underline">
                Terms of Service
              </a>
              &nbsp;|&nbsp;
              <a href={links.vimPP} target="_blank" rel="noreferrer" className="text-underline">
                Privacy Policy
              </a>
            </Text>
            <Tooltip tooltipContent="Click to report an issue" bgColor="white">
              <Button
                className="help-button"
                buttonType="link"
                color="gray"
                onClick={openSupportTab}
                onMouseOver={onMouseOver}
              >
                <img
                  className="help-icon"
                  src={helpIcon}
                  alt="help icon"
                  data-testId="need-help-footer-button"
                />
                Need help?
              </Button>
            </Tooltip>
          </div>
          <SupportTab
            popover={popover}
            onClose={closeSupportTab}
            onSubmit={onContactSupportSubmit}
          />
          <div className="hub-footer-right" />
        </footer>
      </>
    );
  },
);
VimHubFooter.displayName = 'VimHubFooter';
