import { ApplicationManifest } from '@getvim-os/types';
import { OsState, Store } from '..';
import { InjectedApplication } from '../../hub/types/application';
import {
  USER_MANAGEMENT_APPLICATION_ID,
  USER_CENTER_MANIFEST,
} from '@/hub/components/user-management-metadata';

export abstract class BaseStateManager {
  public get store(): Store {
    return this.osState.store;
  }
  constructor(private osState: OsState) {}

  protected getState() {
    return this.store.getState();
  }

  protected getApplication(appId: string): InjectedApplication | undefined {
    if (appId === USER_MANAGEMENT_APPLICATION_ID) {
      return USER_CENTER_MANIFEST;
    }
    return this.getState().applicationsState.applications[appId];
  }

  protected getManifest(appId: string): ApplicationManifest | undefined {
    return this.getApplication(appId)?.manifest;
  }
}
