/* eslint-disable @typescript-eslint/no-unused-vars */
export enum EncounterEnhancements {}

// eslint-disable-next-line @typescript-eslint/ban-types
export type EncounterEnhancementsAPI = {};

/**
 * THIS Types below are used to verify that the EncounterEnhancementsAPI
 * uses all the properties from the enum & the other way around
 *
 * The reason is using mapped types "[EncounterEnhancements.GET_PROBLEM_LIST]()" instead of "getProblemList()"
 * creates bad autocomplete typings
 */
type EnumValues = `${EncounterEnhancements}`;

type VerifyEnumMatchesAPI = keyof EncounterEnhancementsAPI extends EnumValues ? true : never;
type VerifyAPIMatchesEnum = EncounterEnhancements extends keyof EncounterEnhancementsAPI
  ? true
  : never;

// These will cause type errors if there's a mismatch
const enumMatchesAPI: VerifyEnumMatchesAPI = true;

// @ts-expect-error - Will be valid once enum is populated (not working for empty enum)
const apiMatchesEnum: VerifyAPIMatchesEnum = true;
