import { AxiosError } from 'axios';
import { axiosInstance } from './axiosInstance';
import { injectable, inject } from 'inversify';
import { Logger, LoggerFactory } from '@getvim-os/logger';
import { FinalizeSignupResDto } from './types';
export class SignupApiError extends Error {
  constructor(message: string, public status?: number) {
    super(message);
    this.name = 'SignupApiError';
    this.status = status;
  }
}

function handleAxiosError(error: unknown): never {
  if (error instanceof AxiosError) {
    throw new SignupApiError(
      error.response?.data?.message || error.message,
      error.response?.status,
    );
  }
  throw error;
}

@injectable()
export class SignupApi {
  private logger: Logger;

  constructor(@inject(LoggerFactory) loggerFactory: LoggerFactory) {
    this.logger = loggerFactory.create('Signup-API');
  }

  public finalizeSignup = async ({
    sessionId,
    data,
  }: {
    sessionId: string;
    data: {
      ehrUrl: string;
      ehrId: string;
      ehrOS: string;
      ehrUsername: string;
    };
  }): Promise<FinalizeSignupResDto> => {
    try {
      const res = await axiosInstance.post<FinalizeSignupResDto>(
        `/v1/application-signup/${sessionId}/finalize`,
        data,
      );
      return res.data;
    } catch (error) {
      handleAxiosError(error);
    }
  };
}
