import React, { FC, useCallback, useState, FunctionComponent, useEffect, useRef } from 'react';
import classNames from 'classnames';
import { v4 as uuid } from 'uuid';
import {
  ColorNameEnum,
  AlignEnum,
  SizeEnum,
  WeightEnum,
  TextTransformEnum,
} from '../../types/enums';
import Text from '../../atoms/text';
import ClickableDiv from '../../atoms/clickableDiv';
import Tooltip from '../../atoms/tooltip';
import './index.less';
import { AnalyticsHover } from '../..';

const COLLAPSE_DISABLED_TOOLTIP_TEXT = 'No additional information available for this gap';

export interface DetailsCardItemProps {
  id: string;
  label?: string | JSX.Element;
  labelTextTransform?: TextTransformEnum;
  labelWeight?: WeightEnum;
  value?: string | React.ReactNode;
  valueTextTransform?: TextTransformEnum;
  horizontal?: boolean;
  shouldBreakLines?: boolean;
  description?: string;
  TooltipWrapper?: FunctionComponent<{ className: string }>;
  tooltipText?: string;
  onTooltipHover?: (itemLabel: string, tooltipText: string) => void;
  shouldSpanFullWidth?: boolean;
  className?: string;
}

export const WideDetailsCardItem: FC<DetailsCardItemProps> = ({
  id,
  label,
  labelTextTransform,
  labelWeight = WeightEnum.bold,
  value,
  valueTextTransform,
  horizontal,
  shouldBreakLines,
  description,
  TooltipWrapper,
  tooltipText,
  onTooltipHover,
  shouldSpanFullWidth,
  className,
}) => {
  const renderTooltip = () => (
    <AnalyticsHover onHoverAction={onTooltipHover}>
      <Tooltip
        referenceClassName="details-card-item-description-icon"
        placement="top"
        tooltipContent={description}
        bgColor="light"
      >
        <Text className="icon-help" size={SizeEnum['14px']} />
      </Tooltip>
    </AnalyticsHover>
  );
  {
    /* //// CARD ITEM //// */
  }
  return (
    <div
      id={id}
      className={`wide-details-card-item ${horizontal ? 'horizontal' : ''} ${
        shouldSpanFullWidth ? 'span-full-width' : ''
      } ${className}`}
    >
      {tooltipText ? (
        <div className="details-card-item-label-container">
          <Text
            className="item-label"
            colorName={ColorNameEnum.ultraDark}
            weight={labelWeight}
            align={AlignEnum.left}
            text={label}
            inline={horizontal}
            textTransform={labelTextTransform}
          />
          <Tooltip
            referenceClassName="details-card-item-description-icon"
            placement="top"
            tooltipContent={tooltipText}
            bgColor="light"
          >
            <Text className="icon-help" size={SizeEnum['14px']} />
          </Tooltip>
        </div>
      ) : (
        <Text
          className="item-label"
          colorName={ColorNameEnum.ultraDark}
          weight={labelWeight}
          align={AlignEnum.left}
          text={label}
          inline={horizontal}
          textTransform={labelTextTransform}
        />
      )}

      {description &&
        (TooltipWrapper ? (
          <TooltipWrapper className="details-card-item-description-wrapper">
            {renderTooltip()}
          </TooltipWrapper>
        ) : (
          renderTooltip()
        ))}

      <Text
        className="item-value"
        colorName={ColorNameEnum.ultraDark}
        weight={WeightEnum.normal}
        align={AlignEnum.left}
        text={value}
        inline={horizontal}
        textTransform={valueTextTransform}
        shouldBreakLines={shouldBreakLines}
      />
    </div>
  );
};
{
  /* //// CARD ITEMS LIST //// */
}
export interface ListItem {
  label: string;
  value: string;
  options?: {
    shouldBreakLines?: boolean;
  };
}

export interface ComplexListItem extends ListItem {
  childrens?: ListItem[];
}

export interface DetailsCardItemsListProps {
  title?: string;
  items: ComplexListItem[];
}

const unescapeValue = (value): string => {
  if (typeof value === 'string') return value.replace(/\\n/g, '\n');

  return value;
};

export const WideDetailsCardItemsList: FC<DetailsCardItemsListProps> = ({ items, title }) => {
  return (
    <>
      <Text
        className="list-title margin-bottom-5"
        align={AlignEnum.left}
        weight={WeightEnum.bold}
        colorName={ColorNameEnum.ultraDark}
        text={title}
      />
      <ul className="details-card-items-list" title={title}>
        {Object.entries(items).map(([key, item]) => (
          <li key={key}>
            <WideDetailsCardItem id={key} label={item.label} value={item.value} horizontal />
            {item.childrens && (
              <div className="inner-items">
                {Object.entries(item.childrens).map(([internalKey, internalItem]) => (
                  <WideDetailsCardItem
                    key={internalKey}
                    id={internalItem.label}
                    label={internalItem.label}
                    value={unescapeValue(internalItem.value)}
                    shouldBreakLines={internalItem.options?.shouldBreakLines}
                  />
                ))}
              </div>
            )}
          </li>
        ))}
      </ul>
    </>
  );
};

const cardStatusOptions = {
  dismissed: 'dismissed',
  added: 'added',
  default: '',
};

const cardFontSizeOptions = {
  default: '14px',
  small: '12px',
};

const tooltipKeyDown = (e) => {
  if (e.key === 'Enter') {
    e.stopPropagation();
    e.preventDefault();
  }
};

type DetailsCardProps = {
  icon?: string | JSX.Element;
  title?: string | JSX.Element;
  subtitle?: string | JSX.Element;
  description?: string;
  TooltipWrapper?: FunctionComponent<{ className: string }>;
  className?: string;
  /**
   * @param expanded value after toggle
   */
  onToggle?(expanded: boolean): void;
  collapsible?: boolean;
  fontSize?: string;
  cardStatus?: keyof typeof cardStatusOptions;
  permanentVisibleFooter?: any;
  expandOnDemand?: boolean;
  stickyFooter?: boolean;
  actions?: JSX.Element;
  documentViewer?: JSX.Element;
  iconBgColor?: string;
  cardType?: string;
  titleAriaLabel?: string;
  collapsibleEmptyCard?: boolean;
};

const WideDetailsCard: FC<DetailsCardProps> = ({
  children,
  title,
  subtitle,
  description,
  TooltipWrapper,
  className,
  icon,
  onToggle,
  expandOnDemand = false,
  collapsible = true,
  fontSize = 'small',
  cardStatus = 'default',
  permanentVisibleFooter,
  stickyFooter,
  actions,
  documentViewer,
  iconBgColor = '#DDDDDD',
  cardType,
  titleAriaLabel,
  collapsibleEmptyCard = false,
}) => {
  const [expanded, setExpanded] = useState(false);
  const ref = useRef<HTMLDivElement>(null);
  useEffect(() => {
    setExpanded(expandOnDemand);
  }, [expandOnDemand]);

  useEffect(() => {
    if (expanded && expandOnDemand) {
      ref.current?.scrollIntoView({ block: 'nearest', behavior: 'smooth' });
    }
  }, [expanded, expandOnDemand]);

  const isValidChildren =
    (Array.isArray(children) && children.some((node) => React.isValidElement(node))) ||
    (!Array.isArray(children) && React.isValidElement(children));
  const validChildren = !!children && isValidChildren;
  const collapseEnabled = collapsibleEmptyCard || (collapsible && validChildren);

  const onClick = useCallback(
    (event) => {
      const newExpanded = !expanded;
      onToggle?.(newExpanded);
      setExpanded(newExpanded);
      event.stopPropagation();
    },
    [expanded, onToggle],
  );

  const cardTitleKeyDown = useCallback(
    (e) => {
      if (e.target instanceof HTMLElement && e.target.tagName === 'BUTTON') {
        return;
      }

      if (e.key === 'Enter') {
        e.preventDefault();
        onClick(e);
      }
    },
    [onClick],
  );

  const descriptionTooltipId = uuid();

  // //// DESCRIPTION TOOLTIP ////
  const renderTooltip = () => (
    <Tooltip
      referenceClassName="wide-details-card-description-icon"
      className="wide-details-card-description-tooltip"
      placement="top"
      tooltipContent={description}
      bgColor="mainColor"
      width="large"
      id={descriptionTooltipId}
      onKeyDown={tooltipKeyDown}
    >
      <Text className="icon-help" size={SizeEnum['14px']} aria-describedby={descriptionTooltipId} />
    </Tooltip>
  );

  return (
    <ClickableDiv
      className={classNames(
        'wide-details-card',
        className,
        { collapsible: collapseEnabled },
        cardStatusOptions[cardStatus!],
        { 'empty-card': !validChildren },
        { expanded },
        { 'sticky-footer': stickyFooter },
      )}
      ref={ref}
      tabIndex={-1}
    >
      {/* //// CARD HEADER //// */}
      {documentViewer}
      {title && (
        <ClickableDiv
          className={classNames('wide-details-card-header', { expanded })}
          onClick={collapseEnabled ? onClick : undefined}
          onKeyDown={cardTitleKeyDown}
          tabIndex={0}
          aria-expanded={expanded}
          aria-label={`${cardType}; ${titleAriaLabel}`}
        >
          {icon && (
            <div
              className="wide-details-card-title-main-icon"
              style={{ backgroundColor: iconBgColor }}
            >
              <Tooltip
                className="card-icon-tooltip"
                referenceClassName="card-icon-tooltip-wrap"
                tooltipContent={cardType}
                bgColor="mainColor"
                id={descriptionTooltipId}
                onKeyDown={tooltipKeyDown}
                tabIndex={-1}
                portal
              >
                {typeof icon === 'string' ? <i className={icon} /> : <>{icon}</>}
              </Tooltip>
            </div>
          )}
          <Text
            className="wide-details-card-title"
            weight={WeightEnum.medium}
            size={SizeEnum['12px']}
            align={AlignEnum.left}
          >
            {title}
            {description &&
              (TooltipWrapper ? (
                <TooltipWrapper className="wide-details-card-description-icon">
                  {renderTooltip()}
                </TooltipWrapper>
              ) : (
                renderTooltip()
              ))}
          </Text>
          <Text
            className="wide-details-card-subtitle"
            weight={WeightEnum.medium}
            size={SizeEnum['12px']}
            align={AlignEnum.left}
            text={subtitle}
          />
          {actions && <div className="wide-details-card-actions">{actions}</div>}

          {collapsible ? (
            <ClickableDiv
              className="icon-chevron-2 card-arrow"
              onClick={collapseEnabled ? onClick : undefined}
              aria-label={expanded ? 'collapse' : 'expand'}
            />
          ) : (
            <Tooltip
              className="wide-details-card-description-tooltip"
              placement="top"
              tooltipContent={COLLAPSE_DISABLED_TOOLTIP_TEXT}
              bgColor="mainColor"
              width="large"
            >
              <ClickableDiv
                className="icon-chevron-2 card-arrow"
                onClick={collapseEnabled ? onClick : undefined}
                aria-label={expanded ? 'collapse' : 'expand'}
              />
            </Tooltip>
          )}
        </ClickableDiv>
      )}

      {/* //// CARD CONTENT //// */}
      <div>
        {validChildren && (
          <div
            className={classNames(
              !collapseEnabled || expanded ? 'card-content-visible' : 'card-content-hidden',
              'card-content',
            )}
            style={{
              fontSize: cardFontSizeOptions[fontSize!],
              visibility: expanded ? 'visible' : 'hidden',
            }}
          >
            {children}
            {actions && <div className="wide-details-card-actions inner-actions">{actions}</div>}
          </div>
        )}
        {collapsibleEmptyCard && !validChildren && actions && (
          <div
            className={classNames(
              expanded ? 'card-content-visible' : 'card-content-hidden',
              'empty-card-details',
            )}
            style={{
              fontSize: cardFontSizeOptions[fontSize!],
              visibility: expanded ? 'visible' : 'hidden',
            }}
          >
            {actions && <div className="wide-details-card-actions inner-actions">{actions}</div>}
          </div>
        )}
        {permanentVisibleFooter && (
          <div className="permanent-visible-content">{permanentVisibleFooter}</div>
        )}
      </div>
    </ClickableDiv>
  );
};

export default WideDetailsCard;
