/* eslint-disable @typescript-eslint/no-unused-vars */
import { Diagnosis } from '..';

export enum PatientEnhancements {
  GET_PROBLEM_LIST = 'getProblemList',
}

export type PatientEnhancementsAPI = {
  getProblemList(): Promise<Diagnosis[] | undefined>;
};

/**
 * Type checking to ensure PatientEnhancementsAPI and PatientEnhancements enum stay in sync
 * This will cause a type error if there's a mismatch between the enum and API
 */
// Get enum values as a union type
type EnumValues = `${PatientEnhancements}`;

type VerifyEnumMatchesAPI = keyof PatientEnhancementsAPI extends EnumValues ? true : never;
type VerifyAPIMatchesEnum = PatientEnhancements extends keyof PatientEnhancementsAPI ? true : never;

// These will cause type errors if there's a mismatch
const enumMatchesAPI: VerifyEnumMatchesAPI = true;
const apiMatchesEnum: VerifyAPIMatchesEnum = true;
