/* eslint-disable @typescript-eslint/no-unused-vars */
export enum ReferralEnhancements {}

// eslint-disable-next-line @typescript-eslint/ban-types
export type ReferralEnhancementsAPI = {};

/**
 * THIS Types below are used to verify that the PatientEnhancementsAPI
 * uses all the properties from the enum & the other way around
 *
 * The reason is using mapped types "[PatientEnhancements.GET_PROBLEM_LIST]()" instead of "getProblemList()"
 * creates bad autocomplete typings
 */
type EnumValues = `${ReferralEnhancements}`;

type VerifyEnumMatchesAPI = keyof ReferralEnhancementsAPI extends EnumValues ? true : never;
type VerifyAPIMatchesEnum = ReferralEnhancements extends keyof ReferralEnhancementsAPI
  ? true
  : never;

// These will cause type errors if there's a mismatch
const enumMatchesAPI: VerifyEnumMatchesAPI = true;

// @ts-expect-error - Will be valid once enum is populated (not working for empty enum)
const apiMatchesEnum: VerifyAPIMatchesEnum = true;
