import React from 'react';
import classNames from 'classnames';
import './index.less';

interface NotificationsIndicatorProps {
  value: number;
  inactive?: boolean;
  shrink?: boolean;
}

const formatNotifications = (value: number, shrink: boolean = false): string => {
  if (value === 0) return '';
  if (shrink) {
    return value > 9 ? '9+' : value.toString();
  }
  if (value > 999) {
    return '1K+';
  }
  return value.toString();
};

export const NotificationsIndicator: React.FC<NotificationsIndicatorProps> = ({
  value,
  inactive,
  shrink,
}) => {
  const notificationIndication = formatNotifications(value, shrink);
  return (
    <div
      data-testid="notifications-indicator"
      className={classNames('notification', {
        inactive,
        empty: value === 0,
        wide: !shrink && value > 99,
      })}
    >
      {notificationIndication}
    </div>
  );
};
