import { OS_IFRAME_WRAPPER_ID } from '@/infra/consts';
import { isDesktop as isDesktopHostInfo } from '@/infra/hostInfo';
import { CommonConnectors, DesktopConnectors, WebConnectors } from '@getvim-os/barebone/connectors';
import { BareboneMessaging, WebVersioning } from '@getvim-os/barebone/infra';
import { BareboneStorage } from '@getvim-os/barebone/storage';
import { Logger, LoggerFactory, LoggerGlobalConfig } from '@getvim-os/logger';
import { TelemetryGlobalAttributes, VimSpan } from '@getvim-os/telemetry';
import { inject, injectable } from 'inversify';

@injectable()
export class BareboneManager {
  public msiVersion?: string;
  public shouldUseClipPath = false;
  private logger: Logger;

  #installationType!: string;

  public get installationType() {
    return this.#installationType;
  }

  private get isDesktop() {
    return isDesktopHostInfo;
  }

  constructor(
    @inject(BareboneStorage) public storage: BareboneStorage,
    @inject(BareboneMessaging) public messaging: BareboneMessaging,
    @inject(CommonConnectors) public commonConnectors: CommonConnectors,
    @inject(WebConnectors) public webConnectors: WebConnectors,
    @inject(WebVersioning) private webVersioning: WebVersioning,
    @inject(DesktopConnectors) private desktopConnectors: DesktopConnectors,
    @inject(LoggerGlobalConfig) private loggerGlobalConfig: LoggerGlobalConfig,
    @inject(TelemetryGlobalAttributes) private telemetryGlobalAttributes: TelemetryGlobalAttributes,
    @inject(LoggerFactory) private loggerFactory: LoggerFactory,
  ) {
    this.logger = this.loggerFactory.create('barebone-manager');
  }

  public async setup(parentSpan: VimSpan) {
    await parentSpan.startAutoChildSpan('barbone-messageing-setup', {}, async () => {
      await this.messaging.setupMessaging();
    });

    await Promise.all([this.setupVersioning(), this.setupInstallationTypeProperty()]);
  }

  public async setVimOsVisible(visible: boolean) {
    this.logger.info('Setting vim-os iframe wrapper to display', { visible });
    await this.commonConnectors
      .modifyWidget({
        widget: {
          display: visible,
          id: OS_IFRAME_WRAPPER_ID,
        },
      })
      .catch((error) => {
        this.logger.error('Failed to set vim-os iframe wrapper to display false', { error });
      });
  }

  private async setupInstallationTypeProperty() {
    this.#installationType = await this.fetchInstallationType();
  }

  private async fetchInstallationType() {
    if (this.isDesktop) {
      return 'desktop';
    }
    return await this.webConnectors.getInstallationType();
  }

  private async setupVersioning() {
    if (this.isDesktop) {
      this.msiVersion = await this.desktopConnectors.msiVersion();
      this.loggerGlobalConfig.updateMetadata({
        msiVersion: this.msiVersion,
      });
      this.telemetryGlobalAttributes.updateAttributes({ bareboneVersion: this.msiVersion });
    } else {
      await this.webVersioning.initialize();
      this.loggerGlobalConfig.updateMetadata({
        extensionVersion: this.webVersioning.extensionVersion!,
      });
      this.telemetryGlobalAttributes.updateAttributes({
        bareboneVersion: this.webVersioning.extensionVersion!,
      });
    }
  }

  // istanbul ignore next @preserve
  public async configureShouldUseClipPath() {
    if (this.isDesktop) {
      return 'desktop';
    }
    const bareboneSupported =
      (await this.webVersioning.isSDK()) ||
      this.webVersioning.isExtensionVersionGreaterOrEqual('2.0.1507', '1.0.441');
    this.shouldUseClipPath = bareboneSupported;

    const offReason: string[] = [];

    if (!bareboneSupported) {
      offReason.push('Barebone version not supported');
    }
    if (!this.shouldUseClipPath) {
      return offReason.join(', ');
    }
  }
}
